.searchBarList {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 12px 32px;
  justify-content: space-between;
  flex-direction: row;
}

.listItems {
  display: flex;
  width: 50%;

  &:nth-child(2) {
    justify-content: flex-end;
  }
}

.button {
  margin: 0 6px 0 6px;
  border-radius: 15px;
  min-height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 72px;
  text-transform: capitalize;
  background-color: #fff;
  color: #bbbdbc;
  border: none;
  font-weight: bold;
  font-size: 12px;
  text-decoration: none;
  cursor: not-allowed;

  &:nth-child(1) {
    color: #fff;
    margin: 0 6px 0 0;
    background-color: #000;
  }

  &:hover {
    color: #fff;
    background-color: #000;
  }
}

.searchInput {
  min-height: 36px;
  width: 70%;
  min-width: 70%;
  border-radius: 15px;
  border: none;
  padding: 0 12px;
  border: 2px solid grey;
  cursor: pointer;
  &::placeholder {
    color: grey;
  }
}

@media (max-width: 768px) {
  .searchBarList {
    flex-direction: column;
  }
}
